*{
  box-sizing: border-box;
}
body{
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  background-color: black;
  font-family: 'Courier New', Courier, monospace;
}
main{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #0B2434;
  height: clamp(500px, 95vh, 1000px);
  width: clamp(200px, 95vw, 1000px);
}
h1{
  font-size: clamp(32px, 4vh, 64px);
}
h2{
  font-size: clamp(16px, 3vh, 48px);
}
h3{
  font-size: clamp(12px, 3vh, 24px);
}
.game-container{
  display: flex;
  flex-direction: column;
  background-color: #F5F5F5;
  height: 95%;
  width: 95%;
  border-radius: 5%;
  padding: 32px;
  align-items: center;
  text-align: center;
  justify-content: space-between;
  
}
.dice-container{
  display: grid;
  grid-template-rows: repeat(2, clamp(20px, 15vw, 100px));
  grid-template-columns: repeat(5,clamp(20px, 15vw, 100px));
  gap: 12px;
}
.dice{
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  display: flex;
  justify-content: center;
  align-items: center;
  color: black;
  border-radius: 10%;
  border: 2px solid gray;
  box-shadow: 2px 2px 2px gray;
  background-size: cover;
  background-color: whitesmoke;
}
.dice.one{
  background-image: url("./Images/1.png");
}
.dice.two{
  background-image: url("./Images/2.png");
}
.dice.three{
  background-image: url("./Images/3.png");
}
.dice.four{
  background-image: url("./Images/4.png");
}
.dice.five{
  background-image: url("./Images/5.png");
}
.dice.six{
  background-image: url("./Images/6.png");
}
.dice:hover{
  cursor: pointer;
}
.dice:active{
  cursor: default;
}
.game-container button{
  font-size: clamp(24px,4vw,48px);
  background-color: #0B2434;
  color: white;
  padding: 8px 12px 8px 12px;
  border-radius: 12%;
}
.game-container button:hover{
  cursor: pointer;
}
.game-container button:active{
  background-color: red;
  box-shadow: inset 5px 5px 10px -3px rgba(0, 0, 0, 0.7);
  cursor: default;
}